'use client'

import { useState, useEffect } from 'react'
import { 
  Server, Globe, ShieldCheck, Mail, Building2, Calendar, AlertTriangle,
  Search, Plus, Loader2, Settings, RefreshCw, Pencil, Trash2, Filter,
  CheckCircle, Clock, XCircle
} from 'lucide-react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { cn } from '@/lib/utils'
import type { ServiceType } from '@/lib/service-provider-types'
import { SERVICE_TYPES, SERVICE_TYPE_COLORS } from '@/lib/service-provider-types'
import type { TechnicalServiceWithRelations } from '@/lib/technical-service-types-new'
import { SERVICE_STATUSES } from '@/lib/technical-service-types-new'
import Link from 'next/link'
import { ServiceModal } from './components/service-modal'

// ==========================================
// HELPERS
// ==========================================

function getDaysDiff(date: string | null): number | null {
  if (!date) return null
  const today = new Date()
  const renewalDate = new Date(date)
  const diffTime = renewalDate.getTime() - today.getTime()
  return Math.ceil(diffTime / (1000 * 60 * 60 * 24))
}

function ServiceIcon({ type, className }: { type: ServiceType; className?: string }) {
  const icons = {
    hosting: Server,
    domain: Globe,
    ssl: ShieldCheck,
    email: Mail
  }
  const Icon = icons[type] || Server
  return <Icon className={className} />
}

function calculatePrice(service: TechnicalServiceWithRelations): number {
  if (!service.provider?.base_price_usd) return 0
  const basePrice = service.provider.base_price_usd
  const discount = service.discount_percent || 0
  const quantity = service.quantity || 1
  return basePrice * quantity * (1 - discount / 100)
}

function getNextRenewalDate(currentDate: string | null, billingCycle: 'monthly' | 'yearly' = 'yearly'): string {
  const baseDate = currentDate ? new Date(currentDate) : new Date()
  if (billingCycle === 'monthly') {
    baseDate.setMonth(baseDate.getMonth() + 1)
  } else {
    baseDate.setFullYear(baseDate.getFullYear() + 1)
  }
  return baseDate.toISOString().split('T')[0]
}

function formatDate(dateStr: string | null): string {
  if (!dateStr) return '-'
  const date = new Date(dateStr)
  return date.toLocaleDateString('tr-TR', { day: 'numeric', month: 'long', year: 'numeric' })
}

// ==========================================
// PROVIDER BADGES
// ==========================================
const providerColors: Record<string, string> = {
  'siteground': 'bg-indigo-500/20 text-indigo-400 border-indigo-500/20',
  'natro': 'bg-amber-500/20 text-amber-400 border-amber-500/20',
  'veridyen': 'bg-emerald-500/20 text-emerald-400 border-emerald-500/20',
  'default': 'bg-zinc-500/20 text-zinc-400 border-zinc-500/20'
}

function getProviderColor(name: string): string {
  const key = name.toLowerCase()
  return providerColors[key] || providerColors.default
}

// ==========================================
// MAIN COMPONENT
// ==========================================
export default function TeknikHizmetlerPage() {
  const [services, setServices] = useState<TechnicalServiceWithRelations[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const [searchQuery, setSearchQuery] = useState('')
  const [activeFilter, setActiveFilter] = useState<ServiceType | 'all'>('all')
  const [statusFilter, setStatusFilter] = useState<'all' | 'overdue' | 'upcoming' | 'active'>('all')
  const [renewingId, setRenewingId] = useState<string | null>(null)
  
  // Modal state
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [editingService, setEditingService] = useState<TechnicalServiceWithRelations | null>(null)

  useEffect(() => {
    loadData()
  }, [])

  async function loadData() {
    setIsLoading(true)
    try {
      const res = await fetch('/api/technical-services')
      if (res.ok) {
        const data = await res.json()
        // Yenileme tarihine göre sırala (en yakın önce)
        data.sort((a: TechnicalServiceWithRelations, b: TechnicalServiceWithRelations) => {
          const daysA = getDaysDiff(a.renewal_date) ?? Infinity
          const daysB = getDaysDiff(b.renewal_date) ?? Infinity
          return daysA - daysB
        })
        setServices(data)
      }
    } catch (error) {
      console.error('Error loading services:', error)
    } finally {
      setIsLoading(false)
    }
  }

  function handleAddNew() {
    setEditingService(null)
    setIsModalOpen(true)
  }

  function handleEdit(service: TechnicalServiceWithRelations) {
    setEditingService(service)
    setIsModalOpen(true)
  }

  async function handleDelete(id: string) {
    if (!confirm('Bu hizmeti silmek istediğinize emin misiniz?')) return
    
    try {
      const res = await fetch(`/api/technical-services/${id}`, { method: 'DELETE' })
      if (res.ok) {
        await loadData()
      }
    } catch (error) {
      console.error('Error deleting service:', error)
    }
  }

  async function handleRenew(service: TechnicalServiceWithRelations) {
    const rawCycle = service.provider?.billing_cycle || 'yearly'
    const billingCycle = (rawCycle === 'monthly' ? 'monthly' : 'yearly') as 'monthly' | 'yearly'
    const newDate = getNextRenewalDate(service.renewal_date, billingCycle)
    
    if (!confirm(`Yenileme tarihi ${formatDate(newDate)} olarak güncellenecek. Onaylıyor musunuz?`)) return
    
    setRenewingId(service.id)
    try {
      const res = await fetch(`/api/technical-services/${service.id}`, {
        method: 'PATCH',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ renewal_date: newDate })
      })
      
      if (res.ok) {
        await loadData()
      }
    } catch (error) {
      console.error('Error renewing service:', error)
    } finally {
      setRenewingId(null)
    }
  }

  function handleModalClose() {
    setIsModalOpen(false)
    setEditingService(null)
  }

  function handleModalSave() {
    loadData()
  }

  // Filtrele
  const filteredServices = services.filter(service => {
    const matchesSearch = searchQuery === '' || 
      service.identifier.toLowerCase().includes(searchQuery.toLowerCase()) ||
      service.brand?.name?.toLowerCase().includes(searchQuery.toLowerCase()) ||
      service.provider?.name?.toLowerCase().includes(searchQuery.toLowerCase())
    const matchesType = activeFilter === 'all' || service.service_type === activeFilter
    
    const daysDiff = getDaysDiff(service.renewal_date)
    let matchesStatus = true
    if (statusFilter === 'overdue') {
      matchesStatus = daysDiff !== null && daysDiff < 0
    } else if (statusFilter === 'upcoming') {
      matchesStatus = daysDiff !== null && daysDiff >= 0 && daysDiff <= 30
    } else if (statusFilter === 'active') {
      matchesStatus = daysDiff !== null && daysDiff > 30
    }
    
    return matchesSearch && matchesType && matchesStatus
  })

  // Stats hesapla
  const stats = {
    total: services.length,
    brands: new Set(services.map(s => s.brand_id).filter(Boolean)).size,
    upcoming: services.filter(s => {
      const days = getDaysDiff(s.renewal_date)
      return days !== null && days >= 0 && days <= 30
    }).length,
    overdue: services.filter(s => {
      const days = getDaysDiff(s.renewal_date)
      return days !== null && days < 0
    }).length
  }

  // Servis type count
  const typeCounts = {
    hosting: services.filter(s => s.service_type === 'hosting').length,
    domain: services.filter(s => s.service_type === 'domain').length,
    ssl: services.filter(s => s.service_type === 'ssl').length,
    email: services.filter(s => s.service_type === 'email').length
  }

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-[60vh]">
        <div className="flex flex-col items-center gap-4">
          <Loader2 className="h-8 w-8 animate-spin text-cyan-400" />
          <p className="text-sm text-zinc-500">Yükleniyor...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      
      {/* Page Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-xl font-bold text-white">Teknik Hizmetler</h1>
          <p className="text-sm text-zinc-500">Hosting, domain, SSL ve e-posta yönetimi</p>
        </div>
        <div className="flex items-center gap-3">
          <Link href="/ayarlar">
            <Button variant="outline" size="sm">
              <Settings className="w-4 h-4 mr-2" />
              Sağlayıcılar
            </Button>
          </Link>
          <Button onClick={handleAddNew}>
            <Plus className="w-4 h-4 mr-2" />
            Yeni Hizmet
          </Button>
        </div>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-2 lg:grid-cols-4 gap-5">
        {/* Toplam Hizmet */}
        <div className="glass-card rounded-2xl p-5 glow-cyan card-hover">
          <div className="icon-box icon-box-cyan mb-4">
            <Server className="w-6 h-6" />
          </div>
          <p className="text-3xl font-bold text-white mb-1">{stats.total}</p>
          <p className="text-sm text-zinc-500">Toplam Hizmet</p>
        </div>

        {/* Marka */}
        <div className="glass-card rounded-2xl p-5 glow-violet card-hover">
          <div className="icon-box icon-box-violet mb-4">
            <Building2 className="w-6 h-6" />
          </div>
          <p className="text-3xl font-bold text-white mb-1">{stats.brands}</p>
          <p className="text-sm text-zinc-500">Marka</p>
        </div>

        {/* 30 Gün İçinde */}
        <button 
          onClick={() => setStatusFilter(statusFilter === 'upcoming' ? 'all' : 'upcoming')}
          className={cn(
            "glass-card rounded-2xl p-5 card-hover text-left transition-all",
            statusFilter === 'upcoming' ? 'glow-amber ring-2 ring-amber-500/30' : 'glow-amber'
          )}
        >
          <div className="icon-box icon-box-amber mb-4">
            <Calendar className="w-6 h-6" />
          </div>
          <p className="text-3xl font-bold text-white mb-1">{stats.upcoming}</p>
          <p className="text-sm text-zinc-500">30 Gün İçinde</p>
        </button>

        {/* Gecikmiş */}
        <button 
          onClick={() => setStatusFilter(statusFilter === 'overdue' ? 'all' : 'overdue')}
          className={cn(
            "glass-card rounded-2xl p-5 card-hover text-left transition-all",
            statusFilter === 'overdue' ? 'glow-rose ring-2 ring-rose-500/30' : 'glow-rose',
            stats.overdue > 0 && 'animate-pulse'
          )}
        >
          <div className="icon-box icon-box-rose mb-4">
            <AlertTriangle className="w-6 h-6" />
          </div>
          <p className="text-3xl font-bold text-white mb-1">{stats.overdue}</p>
          <p className="text-sm text-zinc-500">Gecikmiş</p>
        </button>
      </div>
      
      {/* Filters */}
      <div className="glass-card rounded-2xl p-4 border border-white/10">
        <div className="flex flex-col lg:flex-row items-stretch lg:items-center gap-4">
          {/* Search */}
          <div className="relative flex-1">
            <Search className="absolute left-4 top-1/2 -translate-y-1/2 w-4 h-4 text-zinc-500" />
            <Input 
              type="text" 
              placeholder="Hizmet, marka veya sağlayıcı ara..." 
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="pl-11"
            />
          </div>
          
          {/* Type Filters */}
          <div className="flex flex-wrap gap-2">
            <button
              onClick={() => setActiveFilter('all')}
              className={cn(
                "h-10 px-4 rounded-xl text-sm font-medium border transition-all",
                activeFilter === 'all' 
                  ? "bg-white/10 border-white/20 text-white" 
                  : "bg-white/5 border-white/10 text-zinc-400 hover:text-white hover:bg-white/10"
              )}
            >
              Tümü ({services.length})
            </button>
            {SERVICE_TYPES.map(({ value, label }) => {
              const isActive = activeFilter === value
              const count = typeCounts[value as ServiceType]
              return (
                <button
                  key={value}
                  onClick={() => setActiveFilter(isActive ? 'all' : value as ServiceType)}
                  className={cn(
                    "h-10 px-4 rounded-xl text-sm font-medium border transition-all flex items-center gap-2",
                    isActive 
                      ? "bg-indigo-500/20 border-indigo-500/30 text-indigo-400" 
                      : "bg-white/5 border-white/10 text-zinc-400 hover:text-white hover:bg-white/10"
                  )}
                >
                  <ServiceIcon type={value as ServiceType} className="w-4 h-4" />
                  {label} ({count})
                </button>
              )
            })}
          </div>
        </div>
      </div>
      
      {/* Empty State */}
      {filteredServices.length === 0 && (
        <div className="glass-card rounded-2xl p-12 border border-white/10 text-center">
          <div className="icon-box icon-box-default w-16 h-16 flex items-center justify-center mx-auto mb-4 animate-float">
            <Server className="w-8 h-8" />
          </div>
          <h3 className="font-semibold text-white mb-2">
            {services.length === 0 ? 'Henüz hizmet eklenmedi' : 'Sonuç bulunamadı'}
          </h3>
          <p className="text-sm text-zinc-500 mb-6">
            {services.length === 0 
              ? 'Hosting, domain, SSL veya e-posta hizmetlerini buradan takip edebilirsin.' 
              : 'Farklı bir arama terimi veya filtre deneyin.'}
          </p>
          {services.length === 0 && (
            <Button onClick={handleAddNew}>
              <Plus className="w-4 h-4 mr-2" />
              İlk Hizmeti Ekle
            </Button>
          )}
        </div>
      )}
      
      {/* Services List */}
      {filteredServices.length > 0 && (
        <div className="glass-card rounded-2xl border border-white/10 overflow-hidden">
          {/* Header */}
          <div className="px-5 py-4 border-b border-white/10 flex items-center justify-between">
            <h2 className="font-semibold text-white">
              {statusFilter === 'all' ? 'Tüm Hizmetler' : 
               statusFilter === 'overdue' ? 'Gecikmiş Hizmetler' : 
               statusFilter === 'upcoming' ? 'Yaklaşan Yenilemeler' : 'Aktif Hizmetler'}
              <span className="text-zinc-500 font-normal ml-2">({filteredServices.length})</span>
            </h2>
            <div className="flex items-center gap-2 text-xs text-zinc-500">
              <Clock className="w-3.5 h-3.5" />
              <span>Yenileme tarihine göre sıralı</span>
            </div>
          </div>
          
          {/* List */}
          <div className="divide-y divide-white/5">
            {filteredServices.map((service) => {
              const daysDiff = getDaysDiff(service.renewal_date)
              const isOverdue = daysDiff !== null && daysDiff < 0
              const isUpcoming = daysDiff !== null && daysDiff >= 0 && daysDiff <= 30
              const isUrgent = daysDiff !== null && daysDiff >= 0 && daysDiff <= 7
              const price = calculatePrice(service)
              const isRenewing = renewingId === service.id
              
              return (
                <div 
                  key={service.id}
                  className={cn(
                    "flex items-center gap-4 p-5 transition-colors group",
                    isOverdue && "bg-rose-500/5 hover:bg-rose-500/10",
                    isUrgent && !isOverdue && "bg-amber-500/5 hover:bg-amber-500/10",
                    isUpcoming && !isUrgent && "hover:bg-white/5",
                    !isOverdue && !isUpcoming && "hover:bg-white/5"
                  )}
                >
                  {/* Icon */}
                  <div className={cn(
                    "icon-box",
                    isOverdue ? "icon-box-rose" :
                    isUpcoming ? "icon-box-amber" : "icon-box-indigo"
                  )}>
                    <ServiceIcon type={service.service_type} className="w-5 h-5" />
                  </div>
                  
                  {/* Info */}
                  <div className="flex-1 min-w-0">
                    <div className="flex items-center gap-2">
                      <p className="font-semibold text-white truncate">{service.identifier}</p>
                      {isOverdue && (
                        <span className="badge badge-rose font-semibold">
                          {Math.abs(daysDiff!)} gün gecikmiş
                        </span>
                      )}
                      {isUrgent && !isOverdue && (
                        <span className="badge badge-amber font-semibold">
                          {daysDiff} gün
                        </span>
                      )}
                      {isUpcoming && !isUrgent && (
                        <span className="badge badge-amber">
                          {daysDiff} gün
                        </span>
                      )}
                    </div>
                    <div className="flex items-center gap-3 mt-1">
                      {service.brand && (
                        <span className="text-xs text-zinc-500">🏢 {service.brand.name}</span>
                      )}
                      {service.provider && (
                        <span className={cn(
                          "text-xs px-2 py-0.5 rounded border",
                          getProviderColor(service.provider.name)
                        )}>
                          {service.provider.name}
                        </span>
                      )}
                      {price > 0 && (
                        <span className="text-xs text-zinc-500 font-mono">
                          ${price.toFixed(0)}/{service.provider?.billing_cycle === 'monthly' ? 'ay' : 'yıl'}
                        </span>
                      )}
                    </div>
                  </div>
                  
                  {/* Date */}
                  <div className="text-right">
                    <p className={cn(
                      "text-sm font-mono",
                      isOverdue ? "text-rose-400" :
                      isUpcoming ? "text-amber-400" : "text-white"
                    )}>
                      {formatDate(service.renewal_date)}
                    </p>
                    {daysDiff !== null && !isOverdue && !isUpcoming && (
                      <p className="text-xs text-zinc-500">{daysDiff} gün kaldı</p>
                    )}
                  </div>
                  
                  {/* Actions */}
                  <div className="flex items-center gap-2">
                    {isOverdue ? (
                      <Button 
                        size="sm"
                        onClick={() => handleRenew(service)}
                        disabled={isRenewing}
                        className="bg-rose-500 hover:bg-rose-600 text-white"
                      >
                        {isRenewing ? (
                          <Loader2 className="w-4 h-4 animate-spin" />
                        ) : (
                          <>
                            <RefreshCw className="w-4 h-4 mr-1" />
                            Yenile
                          </>
                        )}
                      </Button>
                    ) : isUpcoming ? (
                      <Button 
                        size="sm"
                        variant="outline"
                        onClick={() => handleRenew(service)}
                        disabled={isRenewing}
                        className="border-amber-500/30 text-amber-400 hover:bg-amber-500/20"
                      >
                        {isRenewing ? (
                          <Loader2 className="w-4 h-4 animate-spin" />
                        ) : (
                          <>
                            <RefreshCw className="w-4 h-4 mr-1" />
                            Yenile
                          </>
                        )}
                      </Button>
                    ) : (
                      <Button 
                        size="sm"
                        variant="outline"
                        onClick={() => handleRenew(service)}
                        disabled={isRenewing}
                        className="opacity-0 group-hover:opacity-100 transition-opacity"
                      >
                        {isRenewing ? (
                          <Loader2 className="w-4 h-4 animate-spin" />
                        ) : (
                          <>
                            <RefreshCw className="w-4 h-4 mr-1" />
                            Yenile
                          </>
                        )}
                      </Button>
                    )}
                    
                    {/* Edit/Delete */}
                    <div className="flex gap-1 opacity-0 group-hover:opacity-100 transition-opacity">
                      <Button 
                        variant="ghost" 
                        size="icon"
                        onClick={() => handleEdit(service)}
                        className="h-9 w-9"
                      >
                        <Pencil className="w-4 h-4 text-zinc-400" />
                      </Button>
                      <Button 
                        variant="ghost" 
                        size="icon"
                        onClick={() => handleDelete(service.id)}
                        className="h-9 w-9"
                      >
                        <Trash2 className="w-4 h-4 text-rose-400" />
                      </Button>
                    </div>
                  </div>
                </div>
              )
            })}
          </div>
          
          {/* Footer */}
          {filteredServices.length > 10 && (
            <div className="flex items-center justify-between p-5 border-t border-white/10">
              <p className="text-sm text-zinc-500">{filteredServices.length} hizmet gösteriliyor</p>
            </div>
          )}
        </div>
      )}

      {/* Service Modal */}
      <ServiceModal
        isOpen={isModalOpen}
        onClose={handleModalClose}
        onSave={handleModalSave}
        editingService={editingService}
      />
    </div>
  )
}
